import { DefaultUISettings, Service, TCFUISettings, UI_VARIANT } from '@usercentrics/cmp-browser-sdk';
import { Fragment, FunctionalComponent, h } from 'preact';
import {
  SubServiceArrow,
  SubServicesDetails,
  SubServicesDetailsButton,
  SubServicesItem,
  SubServicesText,
} from './SubServices.styles';
import { useEffect } from 'preact/hooks';
import { useSettingsContext } from '../../contexts/SettingsContext';
import { useUiContext } from '../../contexts/UiContext';

interface Props {
  subServicesReader: { read: () => Service[] };
  onOpenSubServiceDetail: (subService: Service) => void;
  onToggleExpandable?: () => void;
}

const SubServices: FunctionalComponent<Props> = ({ subServicesReader, onOpenSubServiceDetail, onToggleExpandable }) => {
  const subServices = subServicesReader.read();
  const { uiVariant } = useUiContext();
  const { settings } = useSettingsContext();

  useEffect(() => {
    if (subServices && onToggleExpandable) {
      onToggleExpandable();
    }
  }, [subServices, onToggleExpandable]);

  const labels =
    uiVariant === UI_VARIANT.TCF
      ? (settings.ui as TCFUISettings).labels.nonTCFLabels.general
      : (settings.ui as DefaultUISettings).labels.general;

  return (
    <Fragment>
      {subServices.map((sub) => {
        return (
          <SubServicesItem>
            <SubServicesText>{sub.name}</SubServicesText>
            <SubServicesDetailsButton
              onClick={(e) => {
                e.stopPropagation();
                onOpenSubServiceDetail(sub);
              }}
            >
              <SubServicesDetails>{labels.details}</SubServicesDetails>
              <SubServiceArrow direction="right" />
            </SubServicesDetailsButton>
          </SubServicesItem>
        );
      })}
    </Fragment>
  );
};

export default SubServices;
